import React from 'react'
import {BrowserRouter as Router, Route, Switch,Link } from 'react-router-dom';
import Home from './Home';
import Dashboard from './Dashboard';
import About from './About';

function App(){
    return (
      <Router>
           <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/dashboard">
            <Dashboard />
          </Route>
        </Switch>
      </Router>
       
    )
}
export default App;
