import React, {useState , useEffect} from 'react'
import Header from './Header'
function Home() {
    const [count, setCount] = useState(0);
    const [name, setName] = useState('Mary');
    return (
        <div>
            <Header/>
            <p>You clicked {count} times</p>
            <button onDoubleClick={() => setCount(count + 1)}>
            Click me
            </button> 

        </div>
     
    )
}

export default Home
